/* Allignment */
.toRight {
    float: right;
    text-align: right;
}
.toLeft {
    float: left;
    text-align: left;
}
.toCenter {
    float: center;
    text-align: center;
}
.verticalAlign {
    position: relative;
    top: 25%;
}
.horizontalAlign {
    position: relative;
    left: 25%;
}
/* Font Style */
.bold {
    font-weight: bold;
}
.fPrimary {
    color: #007bff;
}
.fInfo {
